/*====== Slider =====*/

.slider {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slider__slides {
  position: relative;
  width: 600%;
  height: 100%;
  transition: transform 0.3s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: transform;
}

.slider[data-pos="0"] .slider__slides {
  transform: translateX(0%);
}

.slider[data-pos="1"] .slider__slides {
  transform: translateX(-16.66667%);
}

.slider[data-pos="2"] .slider__slides {
  transform: translateX(-33.33333%);
}

.slider[data-pos="3"] .slider__slides {
  transform: translateX(-50%);
}

.slider[data-pos="4"] .slider__slides {
  transform: translateX(-66.66667%);
}

.slider[data-pos="5"] .slider__slides {
  transform: translateX(-83.33333%);
}

.slider__slide {
  float: left;
  width: 16.66667%;
  height: 100%;
}

.slider__dots {
  display: flex;
  position: absolute;
  bottom: 2.5em;
  left: 50%;
  transform: translateX(-50%);
}

.slider__dot,
.slider__indicator {
  display: block;
  margin: 0 0.6em;
  width: 0.6em;
  height: 0.6em;
  box-shadow: 0 0 2px 0.3vw rgba(255, 255, 255, 0.5);
  border-radius: 100px;
  cursor: pointer;
}

.slider__dot:hover,
.slider__indicator:hover,
.slider__dot.active,
.active.slider__indicator {
  background: white;
}

.slider__indicator {
  position: absolute;
  background: white;
  width: auto;
}

.slider__indicator--left {
  transition: left 0.3s cubic-bezier(0.51, 0.92, 0.24, 1.15), right 0.3s 0.1s cubic-bezier(0.51, 0.92, 0.24, 1.15);
}

.slider__indicator--right {
  transition: left 0.3s 0.1s cubic-bezier(0.51, 0.92, 0.24, 1.15), right 0.3s cubic-bezier(0.51, 0.92, 0.24, 1.15);
}

.slider[data-pos="0"] .slider__indicator {
  left: 0em;
  right: 9em;
}

.slider[data-pos="1"] .slider__indicator {
  left: 1.8em;
  right: 7.2em;
}

.slider[data-pos="2"] .slider__indicator {
  left: 3.6em;
  right: 5.4em;
}

.slider[data-pos="3"] .slider__indicator {
  left: 5.4em;
  right: 3.6em;
}

.slider[data-pos="4"] .slider__indicator {
  left: 7.2em;
  right: 1.8em;
}

.slider[data-pos="5"] .slider__indicator {
  left: 9em;
  right: 0em;
}

