/* MODAL IMAGES */

.modal-btn {
  cursor: pointer;
}

#modal-toggle--1,
#modal-toggle--2,
#modal-toggle--3,
#modal-toggle--4,
#modal-toggle--5,
#modal-toggle--6 {
  display: none;
}

.modal-content,
.modal-backdrop {
  opacity: 0;
  transition: all 0.3s ease-in-out;
  position: fixed;
  z-index: -1;
  overflow: hidden;
  cursor: pointer;
}

.modal-content {
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
  height: auto;
  width: 80%;
  max-width: 900px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content--vert {
  max-width: 600px;
}

.modal-content img {
  display: block;
  cursor: pointer;
}

.modal-backdrop {
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
}

#modal-toggle--1:checked ~ .modal-backdrop,
#modal-toggle--2:checked ~ .modal-backdrop,
#modal-toggle--3:checked ~ .modal-backdrop,
#modal-toggle--4:checked ~ .modal-backdrop,
#modal-toggle--5:checked ~ .modal-backdrop,
#modal-toggle--6:checked ~ .modal-backdrop {
  opacity: 1;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.8);
}

#modal-toggle--1:checked ~ .modal-content,
#modal-toggle--2:checked ~ .modal-content,
#modal-toggle--3:checked ~ .modal-content,
#modal-toggle--4:checked ~ .modal-content,
#modal-toggle--5:checked ~ .modal-content,
#modal-toggle--6:checked ~ .modal-content {
  opacity: 1;
  z-index: 9999;
  pointer-events: auto;
  cursor: auto;
}

.modal-close-btn {
  position: relative;
  display: inline-block;
  cursor: pointer;
  top: -40%;
  left: 50%;
}

.modal-close-btn svg {
  transition: 0.2s;
}

.modal-close-btn svg line {
  stroke-width: 5px;
  stroke: rgba(255, 255, 255, 0.5);
}

.modal-close-btn:hover svg {
  transform: rotate(90deg);
}

